.ms-Button {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: var(--ms-backgroundColor);
  color: var(--ms-textColor);
  font-size: 13px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: none; }
  .ms-Button:hover {
    background-color: var(--ms-backgroundHoverColor); }
  .ms-Button:active {
    background-color: var(--ms-backgroundPressedColor); }
  .ms-Button:focus {
    outline: none;
    box-shadow: 0 0 0 1px var(--ms-focusBorderColor) inset; }
  .ms-Button[aria-disabled] {
    background-color: var(--ms-backgroundDisabledColor);
    color: var(--ms-textDisabledColor); }
